.navbar {
    display: flex;

    gap: 50px;
    justify-content: flex-end;
    background-color: rgb(7, 7, 27);
    color: #d1d1d1;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    border: 1px solid purple;
    box-shadow: 0 0 5px purple, 0 0 10px purple, 0 0 15px purple, 0 0 20px purple, 0 0 40px purple, 0 0 50px purple, 0 0 80px purple;
    z-index: 1;
}

.my_logo {}

.my_logo img {
    width: 45%;
    margin-left: 20px;

}


.resume_btn {
    margin-right: 40px;
    padding: 5 8px;
    border: rgb(171, 16, 209) 2px solid;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    color: rgb(171, 16, 209);
    height: 90%;
    cursor: pointer;
    padding: 8px 8px;
    border-radius: 4px;

}

button span {
    display: inline-block;
    text-align: center;
    margin-right: 5px;
}