.my_img {
    width: 27%;
    box-shadow: 0 0 5px rgb(202, 50, 202), 0 0 10px rgb(143, 34, 143), 0 0 15px rgb(182, 28, 182), 0 0 20px rgb(218, 31, 218);
    float: right;
    border-radius: 500px;
    /* margin-right: 200px; */
    margin-top: 66px;

    position: absolute;
    top: 50%;
    left: 70%;
    transform: translate(-50%, -50%);
    transition: transform 0.2s ease-in-out;
    z-index: 0;
    /* position: relative; */
}

.my_img:hover {

    /* width: 30%; */
    transform: translate(-50%, -50%) scale(1.2);
}

